.cart {
  position: relative;
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    @include response(1200px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__head-left {
    position: relative;
    display: flex;
    flex: 0 0 auto;
    @include response(1200px) {
      order: 2;
    }
  }
  &__total {
    flex: 0 0 auto;
    margin-right: 25px;
    font-weight: bold;
    font-size: 12px;
    background: #a6b6b6;
    border-radius: 4px;
    line-height: 160%;
    padding: 5px 10px;
    color: #fff;
  }
  &__total-remove {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    flex: 0 0 auto;
    @include desktop-hover() {
      .cart__total-remove-icon {
        fill: $purple;
      }
      .cart__total-remove-text {
        color: $purple;
      }
    }
  }
  &__total-remove-icon {
    flex: 0 0 auto;
    margin-right: 10px;
    fill: #a6b6b6;
  }
  &__total-remove-text {
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    text-decoration-line: underline;
    color: #a6b6b6;
    flex: 0 0 auto;
    transition: 0.3s ease-in-out;
  }
  &__head-right {
    flex: 0 0 auto;
  }
  &__title {
    flex: 1 1 auto;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__box {
    display: flex;
    align-items: flex-start;
    @include response(1200px) {
      max-width: 80vw;
      width: 100%;
      margin: 0 auto;
      flex-direction: column;
      align-items: center;
    }
    @include response(767px) {
      max-width: 100%;
      display: block;
    }
  }
  &__list {
    flex: 1 1 auto;
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding-top: 28px;
    padding-bottom: 28px;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      width: calc(100% + 50px);
      margin-left: -25px;
      margin-right: -25px;
      padding-top: 0;
      padding-bottom: 0;
    }
  }
  &__list-inner {
    position: relative;
  }
  &__update {
    position: relative;
    margin-bottom: 5px;
    display: flex;
    justify-content: flex-end;
    padding: 0 20px;
    @include response(767px) {
      padding-top: 20px;
      padding-bottom: 10px;
      justify-content: center;
    }
  }
  &__update-btn {
    flex: 0 0 auto;
    outline: none;
    cursor: pointer;
    background: transparent;
    border: 0;
    text-decoration: none;
    color: $purple;
    font-weight: bold;
    font-size: 16px;
    display: flex;
    align-items: center;
    @include desktop-hover() {
      svg {
        transform: rotate(180deg);
      }
    }
    svg {
      flex: 0 0 auto;
      margin-right: 6px;
      transition: 0.3s ease-in-out;
      fill: $purple;
    }
  }
  &__aside {
    flex: 0 0 375px;
    max-width: 375px;
    width: 100%;
    margin-left: 20px;
    @include response(1200px) {
      margin-left: 0;
    }
    @include response(767px) {
      display: flex;
      flex-direction: column;
    }
  }
  &__item {
    position: relative;
    display: flex;
    align-items: center;
    padding: 10px 20px;
    @include response(767px) {
      padding: 20px 0 20px 20px;
      border-bottom: 1px solid #e9e9e9;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(270deg, #f8f8f8 0%, rgba(248, 248, 248, 0) 100%);
      opacity: 0;
      transition: opacity 0.3s ease-in-out;
    }
    @include desktop-hover() {
      &::before {
        opacity: 1;
      }
    }
    .checkout-success__list & {
      &:nth-child(even) {
        background: #f8f8f8;
      }
      &::before {
        display: none;
      }
    }
  }
  &__item-img {
    display: block;
    position: relative;
    flex: 0 0 150px;
    max-width: 150px;
    width: 150px;
    height: 150px;
    border-radius: 10px;
    overflow: hidden;
    margin-right: 30px;
    @include response(767px) {
      flex: 0 0 100px;
      max-width: 100px;
      width: 100px;
      height: 100px;
      margin-right: 15px;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    &--small {
      flex: 0 0 100px;
      max-width: 100px;
      width: 100px;
      height: 100px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
  }
  &__item-cell {
    position: relative;
    flex: 1 1 auto;
  }
  &__item-title {
    display: block;
    text-decoration: none;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
    margin-bottom: 10px;
    @include desktop-hover() {
      color: $purple;
    }
    .checkout-success__list & {
      margin-bottom: 0;
    }
  }
  &__item-price-box {
    display: flex;
    align-items: center;
    position: relative;
    &--success {
      flex: 0 0 236px;
      max-width: 236px;
      width: 236px;
      justify-content: center;
    }
  }
  &__item-count {
    flex: 0 0 auto;
    position: relative;
    @include response(767px) {
      display: flex;
      margin-top: 5px;
    }
  }
  &__item-remove {
    position: relative;
    flex: 0 0 56px;
    max-width: 56px;
    width: 56px;
    margin-left: 123px;
    align-self: stretch;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    margin-top: -10px;
    margin-bottom: -10px;
    @include response-min(1200px) {
      transition: 0.3s ease-in-out;
    }
    @include response(1200px) {
      margin-left: 0;
    }
    @include response(767px) {
      margin-top: -20px;
      margin-bottom: -20px;
      margin-left: 0;
      padding: 12px 14px;
      background: #f15a89;
      flex: 0 0 auto;
      width: auto;
    }
    @include desktop-hover() {
      background: #f15a89;
      svg {
        fill: #fff;
      }
    }
    svg {
      fill: #a6b6b6;
      @include response(767px) {
        fill: #fff;
      }
    }
  }
  &__size {
    position: relative;
    margin-top: 13px;
    display: flex;
    align-items: center;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__size-value {
    margin-right: 18px;
    flex: 0 0 32px;
    max-width: 32px;
    width: 32px;
    height: 32px;
    border: 1px solid #a6b6b6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 16px;
    text-align: center;
    color: #a6b6b6;
    text-transform: uppercase;
  }
  &__size-box {
    position: relative;
  }
  &__size-title {
    font-size: 12px;
    margin-bottom: 2px;
    font-weight: 500;
    color: #333;
    @include response(767px) {
      text-align: left;
    }
  }
  &__size-row {
    display: flex;
  }
  &__size-row-title {
    color: #808080;
    font-size: 12px;
    flex: 0 0 auto;
    margin-right: 5px;
    font-weight: 500;
  }
  &__size-row-value {
    font-size: 12px;
    margin-bottom: 2px;
    font-weight: 500;
    color: #333;
  }
  &__item-options {
    position: relative;
    text-decoration: none;
    font-size: 14px;
    color: $purple;
    margin-top: 10px;
    @include response(767px) {
      margin-bottom: 15px;
    }
  }
  &__aside-inner {
    position: relative;
    background: #f8f8f8;
    border-radius: 10px;
    padding: 40px 60px 60px;
    margin-bottom: 20px;
    @include response(767px) {
      order: 3;
      flex: 0 0 auto;
      padding: 30px 20px;
    }
  }
  &__aside-title {
    margin-bottom: 44px;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    font-weight: 500;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__aside-row {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
      padding-top: 20px;
      border-top: 1px solid #e9e9e9;
    }
    &--discount {
      position: relative;
      margin-bottom: 40px;
      &::before {
        content: "";
        position: absolute;
        background: #f7edff;
        border-radius: 5px;
        top: -10px;
        bottom: -10px;
        left: -25px;
        right: -25px;
      }
    }
  }
  &__aside-row-title {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    flex: 0 0 50%;
    padding-right: 10px;
    position: relative;
  }
  &__aside-row-value {
    position: relative;
    font-size: 20px;
    line-height: 160%;
    text-align: right;
    color: #000;
    font-weight: 500;
    flex: 0 0 50%;
    .cart__aside-row--discount & {
      color: $purple;
    }
  }
  &__aside-form {
    position: relative;
    margin-bottom: 20px;
  }
  &__aside-form-input {
    width: 100%;
    height: 60px;
    padding-left: 30px;
    padding-right: 100px;
    border: 1px solid #d7d7d7;
    font-size: 16px;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
    outline: none;
    font-weight: 500;
    &:focus {
      border-color: #000;
    }
    &::placeholder {
      color: #808080;
      font-weight: 400;
    }
  }
  &__aside-form-button {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    background: transparent;
    cursor: pointer;
    border: 0;
    color: $purple;
    outline: none;
    @include desktop-hover() {
      color: #000;
    }
  }
  &__aside-btn {
    width: 100%;
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__promo {
    padding: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #d7d7d7;
    border-radius: 10px;
    @include response(767px) {
      margin-bottom: 20px;
      order: 1;
    }
  }
  &__promo-icon {
    flex: 0 0 32px;
    max-width: 32px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 19px;
    background: $purple;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 11px;
      left: 10px;
      transition: 0.3s ease-in-out;
      border-left: 2px solid #fff;
      border-bottom: 2px solid #fff;
      width: 12px;
      height: 7px;
      transform: rotate(-45deg);
    }
  }
  &__promo-box {
    position: relative;
  }
  &__promo-title {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
  }
  &__promo-row {
    display: flex;
    align-items: center;
  }
  &__promo-value {
    font-weight: bold;
    flex: 0 0 auto;
    margin-right: 20px;
    font-size: 14px;
    color: $purple;
  }
  &__promo-remove {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    text-decoration-line: underline;
    color: #a6b6b6;
    cursor: pointer;
    &--mr30 {
      margin-right: 30px;
    }
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      flex: 0 0 auto;
      fill: #a6b6b6;
      margin-right: 10px;
    }
  }
  &__empty {
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 20px 0 78px;
  }
  &__empty-box {
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  &__empty-icon {
    position: relative;
    margin-bottom: 15px;
  }
  &__empty-text {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #808080;
    text-align: center;
  }
}
