.blog {
  position: relative;
  &__category {
    position: relative;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  }
  &__category-list {
    display: flex;
    justify-content: center;
    position: relative;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__category-item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 33.333%;
    max-width: 33.333%;
    width: 100%;
    padding: 20px;
    text-decoration: none;
    position: relative;
    @include response(1200px) {
      padding-left: 10px;
      padding-right: 10px;
    }
    @include response(767px) {
      flex: 0 0 auto;
      max-width: 100%;
      border-bottom: 1px solid #e9e9e9;
      &:last-child {
        border-bottom: 0;
      }
    }
    @include desktop-hover() {
      background: rgba($purple, 0.05);
    }
    &:first-child {
      &::after {
        content: "";
        position: absolute;
        width: 1px;
        height: 30px;
        background: #e9e9e9;
        left: 0;
        top: 50%;
        margin-top: -15px;
        @include response(767px) {
          display: none;
        }
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 30px;
      background: #e9e9e9;
      right: 0;
      top: 50%;
      margin-top: -15px;
      @include response(767px) {
        display: none;
      }
    }
  }
  &__category-item-icon {
    flex: 0 0 auto;
    margin-right: 20px;
    display: flex;
    @include response(1200px) {
      margin-right: 10px;
    }
  }
  &__category-item-text {
    position: relative;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333333;
    @include response(1200px) {
      font-size: 14px;
    }
    &--big {
      font-size: 32px;
      @include response(1200px) {
        font-size: 24px;
      }
    }
  }
  &__decor {
    position: absolute;
    left: 0;
    right: 0;
    height: 170px;
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    &--shadow {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    }
    &--flex {
      height: auto;
      bottom: 0;
      top: 0;
    }
    @include response(767px) {
      display: none;
    }
  }
  &__head {
    position: relative;
    display: flex;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 25px;
    @include response(1200px) {
      padding-top: 20px;
    }
    @include response(767px) {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__head-title {
    flex: 1 1 auto;
    padding-right: 20px;
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      font-size: 30px;
    }
    @include response(767px) {
      text-align: center;
      padding-right: 0;
      margin-bottom: 15px;
      flex: 0 0 100%;
    }
  }
  &__head-btn {
    flex: 0 0 auto;
  }
}
.blog-box {
  padding-top: 75px;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
}
