.select2-container {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  margin: 0;
  position: relative;
  width: 100% !important;
}
.select2-container .select2-selection--single {
  box-sizing: border-box;
  outline: none;
  cursor: pointer;
  display: block;
  height: auto;
  user-select: none;
  -webkit-user-select: none;
}
.select2-container .select2-selection--single .select2-selection__rendered {
  display: block;
  padding-right: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  outline: none;
  font-size: 14px;
  font-weight: 700;
  white-space: nowrap;
  transition: 0.3s ease-in-out;
  .select2-style & {
    outline: 0;
    width: 100%;
    border: 1px solid #d7d7d7;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
    height: 60px;
    line-height: 58px;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 16px;
    color: #333;
    font-weight: 400;
  }
  .select2-style--2 & {
    padding-left: 14px;
    height: 52px;
    line-height: 50px;
  }
  .product-page__select & {
    padding-bottom: 9px;
    border-bottom: 1px solid #a6b6b6;
  }
}
.js-input-parent.active {
  .select2-style .select2-container .select2-selection--single .select2-selection__rendered {
    border-color: $purple;
  }
}
.select2-container--open.select2-container .select2-selection--single .select2-selection__rendered {
  .select2-style & {
    border-color: $purple;
  }
}
.select2-container .select2-selection--single .select2-selection__clear {
  position: relative;
}
.select2-container[dir="rtl"] .select2-selection--single .select2-selection__rendered {
  padding-right: 8px;
  padding-left: 20px;
}
.select2-container .select2-selection--multiple {
  box-sizing: border-box;
  cursor: pointer;
  display: block;
  min-height: 32px;
  user-select: none;
  -webkit-user-select: none;
}
.select2-container .select2-selection--multiple .select2-selection__rendered {
  display: inline-block;
  overflow: hidden;
  padding-left: 8px;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.select2-container .select2-search--inline {
  float: left;
}
.select2-container .select2-search--inline .select2-search__field {
  box-sizing: border-box;
  border: none;
  font-size: 100%;
  margin-top: 5px;
  padding: 0;
}
.select2-container .select2-search--inline .select2-search__field::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

.select2-dropdown {
  box-sizing: border-box;
  display: block;
  position: absolute;
  left: -100000px;
  width: 100%;
  z-index: 1051;
  padding-top: 16px;
  padding-bottom: 16px;
  .select2-style & {
    padding-top: 0;
  }
}

.select2-results {
  display: block;
  margin-right: -20px;
  margin-left: -20px;
  background: #fff;
  box-sizing: border-box;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  .select2-style & {
    margin-left: 0;
    margin-right: 0;
  }
}

.select2-results__options {
  list-style: none;
  margin: 0;
  padding: 0;
}

.select2-results__option {
  padding: 8px 20px;
  user-select: none;
  font-size: 14px;
  line-height: 19px;
  -webkit-user-select: none;
  transition: 0.3s ease-in-out;
  color: #2b2b2f;
  &:last-child {
    margin-bottom: 0;
  }
  @include desktop-hover() {
    background: rgba($purple, 0.1);
  }
}
.select2-results__option[aria-selected] {
  cursor: pointer;
}

.select2-container--open .select2-dropdown {
  left: 0;
  .catalog__head-select & {
    &::before {
      content: "";
      position: absolute;
      left: -56px;
      top: 0;
      bottom: -1px;
      width: 56px;
      background: #fff;
      border-left: 1px solid #e9e9e9;
      border-bottom: 1px solid #e9e9e9;
    }
  }
}

.select2-container--open .select2-dropdown--above {
  border-bottom: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.select2-container--open .select2-dropdown--below {
  // border-top: none;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.select2-search--dropdown {
  display: block;
  padding: 4px;
}
.select2-search--dropdown .select2-search__field {
  padding: 4px;
  width: 100%;
  box-sizing: border-box;
}
.select2-search--dropdown .select2-search__field::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
.select2-search--dropdown.select2-search--hide {
  display: none;
}

.select2-close-mask {
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  min-height: 100%;
  min-width: 100%;
  height: auto;
  width: auto;
  opacity: 0;
  z-index: 99;
  background-color: #fff;
  filter: alpha(opacity=0);
}

.select2-hidden-accessible {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.select2-container--default .select2-selection--single {
  // background-color: #fff;
  border-radius: 4px;
}
.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #2b2b2f;
}
.select2-container--default .select2-selection--single .select2-selection__clear {
