body {
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "Gotham", "Kazimir", sans-serif;
  font-size: 16px;
  line-height: 1;
  min-height: 100vh;
  @include response(1200px) {
    &.pb {
      padding-bottom: 88px;
    }
    &.pb2 {
      padding-bottom: 144px;
    }
  }
}
.wrapper {
  flex: 1 1 auto;
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: hidden;
}
.container {
  position: relative;
  max-width: 1180px;
  width: 100%;
  margin: 0 auto;
  @include response(1200px) {
    padding-left: 25px;
    padding-right: 25px;
  }
}
.main {
  padding-top: 50px;
  @include response(767px) {
    padding-top: 20px;
  }
}
.mb50 {
  margin-bottom: 50px;
  @include response(1200px) {
    margin-bottom: 30px;
  }
}
a {
  @include response-min(1200px) {
    transition: 0.3s ease-in-out;
  }
}
svg {
  transition: 0.3s ease-in-out;
}
* {
  box-sizing: border-box;
  &::after,
  &::before {
    box-sizing: border-box;
  }
}
