.input-style {
  outline: none;
  width: 100%;
  border: 1px solid #d7d7d7;
  border-radius: 10px;
  transition: 0.3s ease-in-out;
  height: 60px;
  padding-left: 30px;
  padding-right: 30px;
  font-size: 16px;
  color: #333;
  transition: 0.3s ease-in-out;
  .js-input-parent.active & {
    border-color: $purple;
  }
  &--textarea {
    height: 180px;
    padding-top: 15px;
    padding-bottom: 15px;
    resize: none;
  }
}

.input-label {
  position: absolute;
  top: 20px;
  left: 22px;
  color: #808080;
  pointer-events: none;
  font-size: 16px;
  transition: .3s ease-in-out;
  background: #fff;
  padding-left: 8px;
  padding-right: 8px;
  z-index: 1;
  .js-input-parent.active & {
    font-size: 12px;
    transform: translateY(-27px);
  }
  &--require {
    &::before {
      content: "*";
      display: inline-block;
      vertical-align: middle;
      color: #f15a89;
      margin-right: 5px;
      padding-top: 3px;
    }
  }
}
