.checkbox {
  position: relative;
  display: block;
  &__input {
    display: none;
    &:checked {
      & + .checkbox__product,
      & + .checkbox__text {
        &::before {
          background: $purple;
          border-color: $purple;
        }
        &::after {
          opacity: 1;
        }
      }
    }
  }
  &__product,
  &__text {
    cursor: pointer;
    display: block;
    padding-left: 45px;
    position: relative;
    font-size: 14px;
    line-height: 32px;
    color: #333;
    @include desktop-hover() {
      color: #000;
    }
    &--small {
      font-size: 12px;
      line-height: 19px;
    }
    &--bold {
      font-weight: 700;
    }
    &--fz16 {
      font-size: 16px;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.3s ease-in-out;
      border: 1px solid #a6b6b6;
      width: 32px;
      height: 32px;
      border-radius: 50%;
    }
    &::after {
      content: "";
      position: absolute;
      top: 11px;
      left: 10px;
      transition: 0.3s ease-in-out;
      border-left: 2px solid #fff;
      border-bottom: 2px solid #fff;
      width: 12px;
      height: 7px;
      transform: rotate(-45deg);
      opacity: 0;
    }
    a {
      color: $purple;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
    .disabled & {
      cursor: initial;
      &::after {
        width: 1px;
        height: 31px;
        background: #a6b6b6;
        opacity: 1;
        border: 0;
        top: 1px;
        left: 15px;
      }
    }
  }
  // &__product {
  //   @include response(767px) {
  //     &::before {
  //       top: 50%;
  //       margin-top: -16px;
  //     }
  //     &::after {
  //       top: 50%;
  //       margin-top: -5px;
  //     }
  //   }
  // }
  &__product-title {
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    .disabled & {
      color: #808080;
    }
  }
  &__product-price-box {
    display: flex;
    align-items: center;
    @include response(1200px) {
      flex-wrap: wrap;
    }
    a {
      font-size: 12px;
      line-height: 1;
      @include response(1200px) {
        flex: 0 0 100%;
        margin-top: 4px;
      }
    }
  }
  &__product-price-new {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #f15a89;
    margin-right: 7px;
    flex: 0 0 auto;
    .disabled & {
      color: #808080;
    }
  }
  &__product-price-old {
    font-weight: bold;
    font-size: 12px;
    line-height: 180%;
    text-decoration-line: line-through;
    color: #333333;
    flex: 0 0 auto;
    margin-right: 15px;
    .disabled & {
      color: #808080;
    }
  }
  &__product-price {
    flex: 0 0 auto;
    margin-right: 15px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    .disabled & {
      color: #808080;
    }
  }
}
