.shop {
  position: relative;
  &__head {
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    @include response(1200px) {
      margin-bottom: 20px;
      box-shadow: none;
    }
  }
  &__box {
    position: relative;
    display: flex;
    @include response(1200px) {
      flex-wrap: wrap;
    }
  }
  &__left {
    flex: 0 0 470px;
    max-width: 470px;
    width: 100%;
    padding: 20px 20px 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    @include response(1200px) {
      flex: 0 0 100%;
      max-width: 100%;
      padding: 0;
      flex-direction: column-reverse;
      align-items: center;
    }
  }
  &__row {
    position: relative;
    @include response(1200px) {
      margin-top: 20px;
      .main-products__title-box {
        margin-bottom: 0;
      }
    }
  }
  &__text-btn {
    display: none;
    @include response(767px) {
      display: block;
      color: #000;
      font-size: 14px;
      font-weight: 700;
      text-decoration: underline;
      flex: 0 0 auto;
    }
  }
  &__text-close {
    display: none;
    .shop__text.active & {
      display: block;
    }
  }
  &__text-open {
    .shop__text.active & {
      display: none;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 25px;
    color: #fff;
    padding: 50px;
    background: $purple;
    position: relative;
    @include response(1200px) {
      padding: 15px 0 0;
      background: transparent;
      color: #808080;
      max-width: 100%;
    }
    @include response(767px) {
      display: flex;
      &.active {
        display: block;
      }
    }
    p {
      margin: 0;
    }
  }
  &__text-inner {
    @include response(767px) {
      position: relative;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      margin-right: 4px;
      .shop__text.active & {
        white-space: normal;
      }
    }
  }
  &__decor {
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    @include response(1200px) {
      display: none;
    }
  }
  &__filter-mobile-box {
    display: none;
    @include response(1200px) {
      flex: 0 0 50%;
      display: block;
    }
  }
  &__filter-mobile-btn {
    @include response(1200px) {
      padding: 13px;
      text-align: center;
      background: #f8f8f8;
      font-weight: bold;
      font-size: 16px;
      line-height: 160%;
      color: #333;
      text-transform: uppercase;
      position: relative;
      &::before {
        content: "";
        position: absolute;
        width: 1px;
        height: 20px;
        background: #c4c4c4;
        right: 0;
        top: 50%;
        margin-top: -10px;
      }
    }
  }
  &__container {
    max-width: 1450px;
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: flex-start;
    padding-top: 30px;
    padding-bottom: 75px;
    @include response(1500px) {
      max-width: 1180px;
    }
    @include response(1200px) {
      padding-left: 25px;
      padding-right: 25px;
      padding-top: 0;
    }
  }
  &__filter {
    flex: 0 0 220px;
    max-width: 220px;
    width: 220px;
    margin-right: 50px;
    @include response(1200px) {
      max-width: 100%;
      width: 100%;
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      margin-right: 0;
      z-index: 101;
      transform: translateX(100%);
      opacity: 0;
      visibility: hidden;
      &.active {
        transform: translateX(0);
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__filter-overlay {
    display: none;
    @include response(1200px) {
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(#000, 0.5);
      transition: 0.3s ease-in-out;
      opacity: 0;
      .shop__filter.active & {
        opacity: 1;
      }
    }
  }
  &__filter-box {
    @include response(1200px) {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 320px;
      background: #fff;
      transition: 0.3s ease-in-out;
      overflow-y: auto;
      transform: translateX(-100%);
      padding-left: 25px;
      padding-right: 25px;
      padding-bottom: 25px;
      &.active {
        transform: translateX(0);
      }
    }
  }
  &__filter-close {
    display: none;
    @include response(1200px) {
      display: block;
      padding: 25px;
      margin-left: -25px;
      margin-right: -25px;
    }
  }
  &__holder {
    flex: 1 1 auto;
  }
  &__holder-head {
    margin-bottom: 20px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include response(1200px) {
      margin-left: -25px;
      margin-right: -25px;
      flex-wrap: wrap;
    }
  }
  &__holder-head-text {
    flex: 1 1 auto;
    padding-right: 20px;
    font-weight: bold;
    font-size: 14px;
    color: #a6b6b6;
    @include response(1200px) {
      flex: 0 0 100%;
      text-align: center;
      order: 3;
      padding-top: 15px;
      padding-right: 0;
    }
  }
  &__select {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    @include response(1200px) {
      background: #f8f8f8;
      flex: 0 0 50%;
      align-self: stretch;
      justify-content: center;
    }
  }
  &__select-title {
    font-weight: bold;
    font-size: 14px;
    color: #a6b6b6;
    flex: 0 0 auto;
    margin-right: 15px;
    @include response(1200px) {
      display: none;
    }
  }
  &__filter-title {
    position: relative;
    margin-bottom: 20px;
    padding-bottom: 20px;
    padding-top: 36px;
    border-bottom: 1px solid #e9e9e9;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      padding-top: 0;
    }
  }
  &__filter-list {
    list-style: none;
    margin: 0 0 40px;
    padding: 0;
    li {
      position: relative;
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    a {
      display: block;
      position: relative;
      text-decoration: none;
      background: #f8f8f8;
      border-radius: 5px;
      padding: 10px 15px;
      font-weight: bold;
      font-size: 14px;
      line-height: 160%;
      color: #333;
      @include desktop-hover() {
        background: $purple;
        color: #f8f8f8;
      }
      &.active {
        background: $purple;
        color: #fff;
        &::before {
          content: "";
          position: absolute;
          right: 18px;
          margin-top: -3px;
          top: 50%;
          width: 6px;
          height: 6px;
          border-bottom: 2px solid #fff;
          border-right: 2px solid #fff;
          transform: rotate(-45deg);
        }
      }
    }
  }
  &__filter-sub-title {
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    margin-bottom: 26px;
  }
  &__filter-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  &__filter-cell {
    flex: 0 0 56px;
    width: 56px;
    height: 42px;
    background: #f8f8f8;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 14px;
    color: #333;
    input {
      border: 0;
      background: transparent;
      width: 30px;
      padding: 0;
      margin-left: 3px;
      font-weight: bold;
      font-size: 14px;
      outline: none;
      color: #333;
    }
  }
  &__filter-separation {
    flex: 0 0 6px;
    height: 2px;
    width: 6px;
    background: #333;
  }
  &__filter-slider-btn {
    height: 42px;
    width: 51px;
    font-size: 14px;
    text-transform: none;
    margin-left: 5px;
    padding: 0;
  }
  &__filter-slider {
    margin-bottom: 25px;
  }
  &__filter-clear-btn {
    display: flex;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    color: #f15a89;
    text-decoration: underline;
    margin-bottom: 40px;
    cursor: pointer;
    @include desktop-hover() {
      text-decoration: none;
    }
    svg {
      fill: #f15a89;
      flex: 0 0 auto;
      margin-right: 10px;
    }
  }
  &__offer {
    position: relative;
  }
  &__offer-box {
    background: #f7edff;
    border-radius: 10px;
    margin-bottom: 10px;
    padding: 47px 35px 29px;
    position: relative;
  }
  &__offer-icon {
    position: absolute;
    top: 27px;
    right: 35px;
  }
  &__offer-title {
    margin-bottom: 11px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333;
  }
  &__offer-descriptions {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    span {
      color: #f15a89;
      font-weight: 500;
    }
  }
  &__offer-btn {
    width: 100%;
  }
}
