.list-style {
  position: relative;
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    margin-bottom: 23px;
    padding-left: 76px;
    padding-right: 10px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &--2 {
    li {
      padding-left: 40px;
    }
  }
  &__row {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 4px;
  }
  &__count {
    flex: 0 0 auto;
    margin-right: 5px;
    font-weight: 700;
    color: #a6b6b6;
    font-size: 16px;
  }
  &__title {
    flex: 1 1 auto;
    font-weight: 700;
    color: #333;
    font-size: 16px;
  }
  &__descriptions {
    font-size: 13px;
    line-height: 160%;
    color: #333333;
    font-weight: 500;
  }
  &__color {
    color: $purple;
  }
  &__link {
    font-size: 12px;
    line-height: 11px;
    color: #6c41bf;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__size {
    display: flex;
    align-items: center;
  }
  &__size-value {
    flex: 0 0 32px;
    width: 32px;
    height: 32px;
    background: #fff;
    border: 1px solid #a6b6b6;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    text-transform: uppercase;
    color: #a6b6b6;
    margin-right: 18px;
    &--small {
      font-size: 12px;
      font-weight: 500;
      text-transform: none;
    }
  }
  &__size-info {
    position: relative;
    flex: 1 1 auto;
    font-weight: 500;
  }
}
