.subscribe-box {
  position: relative;
  background: #f8f8f8;
  padding-top: 53px;
  padding-bottom: 65px;
  @include response(1200px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__head {
    display: flex;
    align-items: flex-end;
    justify-content: center;
    margin-bottom: 20px;
    @include response(767px) {
      display: block;
    }
  }
  &__title {
    max-width: 345px;
    flex: 0 0 345px;
    width: 100%;
    margin-left: 15px;
    margin-right: 15px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    text-align: right;
    color: #333;
    @include response(767px) {
      max-width: 100%;
      text-align: center;
      margin-bottom: 30px;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__right {
    max-width: 330px;
    flex: 0 0 330px;
    width: 100%;
    margin-left: 15px;
    margin-right: 15px;
    position: relative;
    @include response(767px) {
      max-width: 100%;
      text-align: center;
      margin-left: 0;
      margin-right: 0;
    }
  }
  &__descriptions {
    font-size: 14px;
    line-height: 180%;
    color: #333;
    margin-bottom: 15px;
  }
  &__attention {
    color: #f15a89;
    font-size: 14px;
    line-height: 180%;
  }
  &__form {
    position: relative;
  }
  &__input-box {
    position: relative;
    width: 300px;
    margin: 0 auto 20px;
    @include response(767px) {
      margin-bottom: 30px;
    }
  }
  &__input {
    border: 1px solid #d7d7d7;
    border-radius: 50px;
    transition: .3s ease-in-out;
    outline: none;
    width: 100%;
    padding-left: 28px;
    padding-right: 28px;
    font-size: 15px;
    color: #333;
    height: 54px;
    background: transparent;
    font-weight: 500;
    &::placeholder {
      color: #808080;
      font-weight: 400;
    }
    &:focus {
      border-color: #333;
    }
  }
  &__checkbox {
    position: relative;
    margin-bottom: 22px;
    display: flex;
    justify-content: center;
    &--250 {
      max-width: 250px;
    }
  }
  &__form-btn-box {
    text-align: center;
  }
  &__decor {
    position: absolute;
    bottom: 0;
    pointer-events: none;
    &--left {
      left: 0;
      @include response(767px) {
        display: none;
      }
    }
    &--right {
      right: 0;
      @include response(1200px) {
        display: none;
      }
    }
  }
}
