.counter {
  position: relative;
  display: flex;
  align-items: center;
  &__btn {
    position: relative;
    flex: 0 0 22px;
    max-width: 22px;
    width: 22px;
    height: 22px;
    border-radius: 5px;
    background: #fff;
    cursor: pointer;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      background: #f15a89;
      &::before,
      &::after {
        background: #fff;
      }
    }
    &::before {
      content: "";
      position: absolute;
      width: 10px;
      height: 2px;
      background: #333;
      border-radius: 5px;
      left: 50%;
      margin-left: -5px;
      top: 50%;
      margin-top: -1px;
      transition: 0.3s ease-in-out;
    }
    &--plus {
      &::after {
        content: "";
        position: absolute;
        height: 10px;
        width: 2px;
        background: #333;
        border-radius: 5px;
        left: 50%;
        margin-left: -1px;
        top: 50%;
        margin-top: -5px;
        transition: 0.3s ease-in-out;
      }
    }
  }
  &__input {
    flex: 0 0 30px;
    width: 30px;
    max-width: 30px;
    background: transparent;
    font-size: 20px;
    text-align: center;
    color: #333;
    font-weight: 500;
    padding: 0;
    outline: none;
    border: 0;
  }
}
