.blog-btns {
  position: relative;
  display: flex;
  margin-left: -17px;
  @include response(767px) {
    flex-wrap: wrap;
  }
  &__btn {
    flex: 0 0 calc(50% - 17px);
    max-width: calc(50% - 17px);
    width: 100%;
    margin-left: 17px;
    background: #ffffff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    padding: 13px 25px;
    display: flex;
    align-items: center;
    text-decoration: none;
    font-weight: bold;
    font-size: 18px;
    line-height: 160%;
    color: #333333 !important;
    @include response(767px) {
      flex: 0 0 calc(100% - 17px);
      max-width: calc(100% - 17px);
      margin-bottom: 17px;
    }
    @include desktop-hover() {
      background: #f7edff;
    }
  }
  &__btn-icon {
    flex: 0 0 46px;
    width: 46px;
    height: 46px;
    border: 2px solid rgba($purple, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 16px;
    border-radius: 50%;
  }
}
