.checkout-success {
  position: relative;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-bottom: 50px;
  }
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    @include response(1200px) {
      font-size: 20px;
      margin-bottom: 20px;
    }
    svg {
      flex: 0 0 auto;
      margin-right: 23px;
    }
  }
  &__box {
    position: relative;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    max-width: 780px;
    width: 100%;
    margin: 0 auto 30px;
    padding-bottom: 75px;
    @include response(1200px) {
      box-shadow: none;
      padding-bottom: 0;
    }
  }
  &__head {
    padding: 50px 50px 22px;
    position: relative;
    text-align: center;
    margin-bottom: 42px;
    @include response(1200px) {
      padding: 0 0 20px;
      margin-bottom: 20px;
    }
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50px;
      right: 50px;
      height: 1px;
      background: #e9e9e9;
      @include response(1200px) {
        left: 0;
        right: 0;
      }
    }
  }
  &__head-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    margin-bottom: 10px;
  }
  &__head-text {
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    a {
      color: $purple;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__number-box {
    display: flex;
    justify-content: center;
    margin-bottom: 35px;
  }
  &__number {
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    color: #333;
    background: #f7edff;
    border-radius: 4px;
    padding: 6px 20px;
  }
  &__small-title {
    text-align: center;
    margin-bottom: 22px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
  }
  &__table {
    margin-bottom: 40px;
    display: flex;
    @include response(1200px) {
      display: block;
      max-width: 60vw;
      width: 100%;
      margin: 0 auto;
    }
    @include response(767px) {
      margin-left: -25px;
      margin-right: -25px;
      width: auto;
      max-width: calc(100% + 50px);
    }
  }
  &__table-box {
    width: 100%;
    &:nth-child(2) {
      .checkout-success__table-cell {
        &:nth-child(1) {
          flex: 0 0 180px;
          max-width: 180px;
          width: 180px;
          @include response(1200px) {
            flex: 0 0 163px;
            max-width: 163px;
            width: 163px;
          }
        }
      }
    }
  }
  &__table-row {
    display: flex;
    &:nth-child(even) {
      background: #f8f8f8;
    }
  }
  &__table-cell {
    color: #333;
    font-size: 16px;
    padding-right: 10px;
    padding-top: 22px;
    padding-bottom: 22px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &:nth-child(1) {
      flex: 0 0 215px;
      max-width: 215px;
      width: 215px;
      padding-left: 50px;
      @include response(1200px) {
        padding-left: 25px;
        flex: 0 0 163px;
        max-width: 163px;
        width: 163px;
      }
    }
    &:nth-child(2) {
      font-weight: 700;
      max-width: 175px;
      @include response(1200px) {
        flex: 1 1 auto;
        max-width: none;
      }
    }
  }
  &__list {
    position: relative;
    @include response(1200px) {
      display: none;
    }
  }
  &__footer {
    background: #f8f8f8;
    border-radius: 10px;
    position: relative;
    padding: 50px;
    max-width: 780px;
    width: 100%;
    margin: 0 auto;

  }
  &__footer-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 28px;
    @include response(1200px) {
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__total {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__total-title {
    flex: 0 0 auto;
    font-size: 28px;
    color: #333333;
    margin-right: 5px;
  }
  &__total-value {
    flex: 0 0 auto;
    font-size: 28px;
    color: #333333;
    font-weight: 700;
  }
  &__download {
    display: flex;
    align-items: center;
    text-decoration: none;
    @include desktop-hover() {
      .checkout-success__download-text {
        text-decoration: none;
      }
    }
  }
  &__download-icon {
    flex: 0 0 auto;
    margin-right: 15px;
    svg {
      fill: #f15a89;
    }
  }
  &__download-text {
    font-size: 14px;
    line-height: 160%;
    color: #333333;
    text-decoration: underline;
  }
  &__footer-text {
    font-size: 16px;
    line-height: 160%;
    position: relative;
    padding-left: 52px;
    line-height: 32px;
    svg {
      position: absolute;
      left: 0;
      top: 0;
      margin-right: 20px;
    }
  }
}
