.review2 {
  position: relative;
  &__nav {
    position: relative;
    border-bottom: 1px solid #c4c4c4;
  }
  &__options {
    position: relative;
  }
  &__head {
    display: flex;
    align-items: center;
    padding-top: 50px;
    padding-bottom: 50px;
    @include response(767px) {
      flex-direction: column;
      justify-content: center;
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
  &__title {
    flex: 0 0 auto;
    margin-right: 30px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
  &__head-rate {
    display: flex;
    align-items: center;
  }
  &__list {
    position: relative;
    margin-bottom: 30px;
  }
  &__item {
    position: relative;
    margin-bottom: 20px;
    background: #f7edff;
    border-radius: 10px;
    padding: 50px;
    @include response(1200px) {
      padding: 20px;
    }
  }
  &__item-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 25px;
  }
  &__item-title {
    flex: 1 1 auto;
    padding-right: 20px;
    font-size: 20px;
    line-height: 160%;
    color: #333333;
    font-weight: 500;
  }
  &__item-rate {
    flex: 0 0 auto;
  }
  &__item-body {
    position: relative;
  }
  &__item-text {
    margin-bottom: 25px;
    font-size: 14px;
    line-height: 180%;
    color: #333333;
    p {
      margin: 0;
    }
  }
  &__item-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item-name {
    flex: 0 0 auto;
    margin-right: 10px;
    font-size: 14px;
    line-height: 180%;
    color: #333333;
    font-weight: 500;
    @include response(767px) {
      margin-bottom: 4px;
    }
  }
  &__item-verified {
    padding: 7px 10px;
    background: $purple;
    font-size: 12px;
    color: #fff;
    border-radius: 4px;
    text-transform: uppercase;
    flex: 0 0 auto;
  }
  &__item-date {
    font-size: 14px;
    line-height: 180%;
    color: #808080;
    margin-left: 30px;
    @include response(767px) {
      margin-left: 0;
      margin-top: 10px;
    }
  }
  &__info {
    display: flex;
    align-items: center;
  }
  &__info-item {
    color: #a6b6b6;
    text-decoration: none;
    margin-right: 12px;
    flex: 0 0 auto;
    font-size: 13px;
    font-weight: 500;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 1px;
      height: 12px;
      background: #a6b6b6;
      right: -7px;
      top: 1px;
    }
    &:last-child {
      &::before {
        display: none;
      }
    }
    @include desktop-hover() {
      color: $purple;
    }
    &.active {
      color: $purple;
    }
  }
  &__item-footer-left {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
    position: relative;
    @include response(767px) {
      display: block;
    }
  }
  &__item-btn {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__item-btn-icon {
    flex: 0 0 auto;
    margin-right: 8px;
  }
  &__item-btn-text {
    font-size: 14px;
    line-height: 180%;
    color: #333333;
    font-weight: 500;
    margin-right: 18px;
  }
  &__item-btn-arrow {
    flex: 0 0 auto;
    .review2__item.active & {
      transform: scaleY(-1);
    }
  }
  &__item-first-letter {
    font-size: 20px;
    font-weight: 500;
    &--color {
      color: $purple;
    }
  }
  &__drop {
    margin-top: 30px;
    padding-top: 30px;
    border-top: 1px solid #fff;
    display: none;
    .review2__item.active & {
      display: block;
    }
  }
  &__drop-inner {
    padding-left: 30px;
    border-left: 5px solid $purple;
    @include response(767px) {
      padding-left: 15px;
    }
  }
  &__drop-text {
    margin-bottom: 27px;
    font-size: 14px;
    line-height: 180%;
    color: #333;
  }
  &__drop-bottom {
    display: flex;
    align-items: center;
    @include response(767px) {
      display: block;
    }
  }
}
