.author {
  position: relative;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  z-index: 1;
  &__decor {
    position: absolute;
    top: 0;
    @include response(1200px) {
      display: none;
    }
    &--1 {
      left: 0;
    }
    &--2 {
      right: 0;
    }
  }
  &__box {
    position: relative;
    display: flex;
    @include response(1200px) {
      display: block;
    }
  }
  &__left {
    flex: 0 0 470px;
    max-width: 470px;
    width: 100%;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    @include response(1200px) {
      max-width: 100%;
      padding-right: 0;
      flex-direction: column-reverse;
      align-items: center;
    }
  }
  &__row {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    @include response(1200px) {
      margin-bottom: 0;
      margin-top: 20px;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__ava {
    margin-right: 30px;
    flex: 0 0 100px;
    max-width: 100px;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    overflow: hidden;
    padding: 6px;
    background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
    @include response(1200px) {
      margin-right: 0;
    }
    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
  &__info {
    flex: 1 1 auto;
    @include response(1200px) {
      flex: 0 0 100%;
    }
  }
  &__name {
    font-weight: bold;
    font-size: 40px;
    line-height: 160%;
    color: #333;
    margin-bottom: 4px;
    @include response(1200px) {
      text-align: center;
      flex: 0 0 100%;
    }
  }
  &__holder {
    display: flex;
    align-items: center;
    @include response(1200px) {
      justify-content: center;
    }
  }
  &__label {
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background: #f15a89;
    border-radius: 4px;
    padding: 5px 10px;
    flex: 0 0 auto;
    margin-right: 12px;
  }
  &__text {
    flex: 1 1 auto;
    position: relative;
    background: #f8f8f8;
    padding: 60px 50px;
    font-size: 14px;
    line-height: 25px;
    color: #333;
    @include response(1200px) {
      margin-left: -25px;
      margin-right: -25px;
      padding: 30px 25px;
      background: #fff;
      text-align: center;
    }
    p {
      margin: 0;
    }
  }
}
