.sources {
  position: relative;
  border-top: 1px solid #d7d7d7;
  border-bottom: 1px solid #d7d7d7;
  margin-bottom: 50px;
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 33px 50px;
    @include response(767px) {
      padding: 20px 15px;
    }
  }
  &__head-left {
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    flex: 1 1 auto;
    padding-right: 10px;
  }
  &__head-btn {
    flex: 0 0 auto;
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 160%;
    color: #f15a89;
    cursor: pointer;
    position: relative;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      color: $purple;
      &::before,
      &::after {
        background: $purple;
      }
    }
    .active.sources & {
      &::before {
        opacity: 1;
      }
    }
    &::before {
      content: "";
      position: absolute;
      right: 7px;
      top: 50%;
      margin-top: -8px;
      height: 15px;
      width: 1px;
      background: #f15a89;
      transition: 0.3s ease-in-out;
      opacity: 0;
    }
    &::after {
      content: "";
      display: inline-block;
      height: 1px;
      width: 15px;
      background: #f15a89;
      flex: 0 0 auto;
      margin-left: 10px;
      transition: 0.3s ease-in-out;
    }
  }
  &__body {
    padding-bottom: 50px;
    @include response(767px) {
      padding-bottom: 30px;
    }
  }
  &__row {
    padding-left: 46px;
    position: relative;
    padding-bottom: 20px;
    margin-bottom: 20px;
    @include response(767px) {
      padding-left: 25px;
    }
    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      &::after {
        display: none;
      }
    }
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      right: 24px;
      left: 46px;
      height: 1px;
      background: #f7edff;
    }
    &::before {
      content: "";
      position: absolute;
      background: #a6b6b6;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      left: 0;
      top: 7px;
    }
  }
}
