.checkout {
  position: relative;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-bottom: 50px;
  }
  &__back {
    position: relative;
    text-decoration: none;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    color: #a6b6b6;
    padding-left: 17px;
    flex: 0 0 auto;
    margin-right: 30px;
    @include desktop-hover() {
      color: $purple;
      &::before {
        border-color: $purple;
      }
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 7px;
      width: 7px;
      height: 7px;
      border-bottom: 2px solid #a6b6b6;
      border-left: 2px solid #a6b6b6;
      transform: rotate(45deg);
      transition: 0.3s ease-in-out;
    }
  }
  &__continue {
    position: relative;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    padding-left: 20px;
    color: $purple;
    @include desktop-hover() {
      text-decoration: none;
    }
    &::before {
      content: "";
      position: absolute;
      width: 10px;
      height: 2px;
      background: $purple;
      margin-top: -1px;
      top: 50%;
      left: 0;
    }
    &::after {
      content: "";
      position: absolute;
      height: 10px;
      width: 2px;
      background: $purple;
      top: 50%;
      margin-top: -5px;
      left: 4px;
    }
  }
  &__box {
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    background: #fff;
    padding: 35px 50px 75px;
    flex: 1 1 auto;
    @include response(1200px) {
      margin-bottom: 30px;
    }
    @include response(767px) {
      padding: 0 10px 20px;
      box-shadow: none;
    }
  }
  &__step {
    position: relative;
  }
  &__step-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding-bottom: 20px;
    padding-top: 20px;
    border-bottom: 1px solid #e9e9e9;
    padding-right: 30px;
    &::before {
      content: "";
      position: absolute;
      width: 6px;
      height: 6px;
      border-bottom: 2px solid #a6b6b6;
      border-right: 2px solid #a6b6b6;
      right: 0;
      top: 30px;
      transform: rotate(45deg);
    }
    .checkout__step.active & {
      &::before {
        border-color: #000;
      }
    }
  }
  &__step-count {
    position: relative;
    flex: 0 0 32px;
    max-width: 32px;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    font-weight: bold;
    font-size: 16px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-right: 20px;
    background: #a6b6b6;
    .checkout__step.active & {
      background: $purple;
    }
    .checkout__step.active.done & {
      background: #f15a89;
      font-size: 0;
      &::before {
        content: "";
        position: absolute;
        height: 12px;
        width: 7px;
        border-bottom: 2px solid #fff;
        border-right: 2px solid #fff;
        transform: rotate(45deg);
        left: 12px;
        top: 8px;
      }
    }
  }
  &__step-title {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    font-weight: 500;
    color: #a6b6b6;
    .checkout__step.active & {
      color: #333;
    }
  }
  &__form {
    position: relative;
    padding-top: 39px;
    .checkout__step.active.done & {
      display: none;
    }
    @include response(767px) {
      padding-top: 29px;
    }
  }
  &__form-row {
    display: flex;
    margin-left: -20px;
    margin-bottom: 25px;
    flex-wrap: wrap;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__form-cell {
    flex: 0 0 calc(50% - 20px);
    max-width: calc(50% - 20px);
    width: 100%;
    margin-left: 20px;
    position: relative;
    @include response(767px) {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
      margin-bottom: 25px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &--100 {
      flex: 0 0 calc(100% - 20px);
      max-width: calc(100% - 20px);
    }
  }
  &__form-checkbox {
    margin-top: 30px;
    margin-bottom: 30px;
  }
  &__form-btn {
    width: 100%;
    height: 66px;
  }
  &__info {
    position: relative;
    padding-top: 25px;
    display: none;
    .checkout__step.active.done & {
      display: block;
    }
  }
  &__info-row {
    display: flex;
    margin-bottom: 25px;
    @include response(1200px) {
      flex-direction: column;
    }
  }
  &__info-cell {
    flex: 0 0 33.333%;
    max-width: 33.333%;
    width: 100%;
    padding-right: 10px;
    display: flex;
    @include response(1200px) {
      flex: 0 0 100%;
      max-width: 100%;
      margin-bottom: 20px;
      padding-left: 44px;
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &__info-icon {
    flex: 0 0 44px;
    width: 44px;
    max-width: 44px;
    padding-top: 4px;
    svg {
      fill: $purple;
    }
    &--contact {
      flex: 0 0 30px;
      width: 30px;
      max-width: 30px;
      svg {
        fill: #a6b6b6;
      }
    }
  }
  &__info-text {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    font-style: normal;
  }
  &__info-contact {
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
  }
  &__step-head-box {
    display: flex;
    align-items: center;
  }
  &__edit {
    cursor: pointer;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    line-height: 160%;
    text-decoration-line: underline;
    color: #a6b6b6;
    display: none;
    .checkout__step.active.done & {
      display: block;
    }
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__form-2 {
    position: relative;
    display: none;
    .checkout__step.active & {
      display: block;
    }
  }
  &__address {
    margin-bottom: 25px;
  }
  &__address-title {
    margin-bottom: 25px;
    font-weight: bold;
    font-size: 14px;
    line-height: 180%;
    color: #333;
    padding-top: 25px;
  }
  &__address-box {
    position: relative;
    background: #f8f8f8;
    border-radius: 10px;
    padding: 17px 30px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__address-value {
    font-size: 16px;
    line-height: 160%;
    color: #333;
    max-width: 228px;
    width: 228px;
  }
  &__address-btns {
    display: flex;
    align-items: center;
  }
  &__pay {
    position: relative;
  }
  &__pay-row {
    padding-top: 30px;
    margin-bottom: 30px;
    display: flex;
    align-items: center;
    @include response(767px) {
      flex-wrap: wrap;
    }
  }
  &__pay-title {
    flex: 0 0 auto;
    margin-right: 30px;
    font-weight: bold;
    font-size: 14px;
    line-height: 180%;
    color: #333;
    @include response(767px) {
      flex: 0 0 100%;
      margin-right: 0;
      margin-bottom: 25px;
    }
  }
  &__pay-cell {
    flex: 0 0 auto;
    display: flex;
  }
  &__pay-item {
    margin-right: 40px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__pay-box {
    position: relative;
    display: flex;
    margin-bottom: 16px;
    margin-left: -20px;
    @include response(1200px) {
      flex-wrap: wrap;
    }
  }
  &__pay-input {
    position: relative;
    margin-left: 20px;
    width: 100%;
    @include response(1200px) {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &--330 {
      flex: 0 0 330px;
      max-width: 330px;
      width: 330px;
      @include response(1200px) {
        flex: 0 0 calc(100% - 20px);
        max-width: calc(100% - 20px);
        width: calc(100% - 20px);
      }
    }
  }
  &__pay-btn {
    width: 100%;
    height: 66px;
    @include response(767px) {
      height: 52px;
    }
  }
  &__pay-btn-box {
    @include response(767px) {
      padding: 20px 25px;
      background: #fff;
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 1;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    }
  }
  &__pay-mobile {
    display: none;
    @include response(767px) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
    }
  }
  &__pay-mobile-all {
    padding: 6px 10px;
    font-weight: bold;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background: #a6b6b6;
    border-radius: 4px;
    flex: 0 0 auto;
  }
  &__pay-total {
    display: flex;
    align-items: center;
    flex: 0 0 auto;
  }
  &__pay-total-text {
    flex: 0 0 auto;
    margin-right: 10px;
    font-size: 12px;
    line-height: 160%;
    color: #333333;
    @include response(390px) {
      font-size: 9px;
    }
  }
  &__pay-total-value {
    font-size: 20px;
    line-height: 160%;
    color: #333333;
    font-weight: 700;
  }
}
