.faq {
  position: relative;
  margin-bottom: 50px;
  &__item {
    margin-bottom: 20px;
    position: relative;
    border: 1px solid #a6b6b6;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
    @include desktop-hover() {
      border-color: $purple;
    }
    &.active {
      border-color: $purple;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__head {
    position: relative;
    padding: 23px 70px 23px 23px;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    cursor: pointer;
    @include response(767px) {
      padding: 20px 40px 20px 20px;
    }
    svg {
      position: absolute;
      right: 33px;
      top: 50%;
      margin-top: -3px;
      @include response(767px) {
        right: 15px;
      }
    }
    .faq__item.active & {
      svg {
        transform: rotate(180deg);
      }
    }
  }
  &__body {
    padding: 0 43px 27px;
    display: none;
    @include response(767px) {
      padding-left: 20px;
      padding-right: 20px;
    }
    .faq__item.active & {
      display: block;
    }
  }
}
