.btn-top {
  position: relative;
  display: flex;
  cursor: pointer;
  height: 40px;
  @include desktop-hover() {
    .btn-top__arrow {
      transform: translateY(-4px);
    }
  }
  &__arrow {
    position: absolute;
    left: 50%;
    margin-left: -5px;
    bottom: 40%;
    stroke: #fff;
    stroke-width: 2;
    fill: none;
    z-index: 1;
  }
  &__bg {
    position: absolute;
    left: 50%;
    bottom: 0;
    margin-left: -960px;
    width: 1920px;
    height: 40px;
  }
}