.reviews {
  position: relative;
  padding-top: 50px;
  padding-bottom: 25px;
  @include response(1200px) {
    display: none;
  }
  &__title {
    text-align: center;
    margin-bottom: 13px;
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 72px;
  }
  &__link {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    text-decoration-line: underline;
    color: #a6b6b6;
    margin-left: 40px;
    @include desktop-hover() {
      text-decoration: none;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-left: -20px;
  }
  &__item {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    width: 100%;
    margin-left: 20px;
    margin-bottom: 20px;
    background: #f7edff;
    border-radius: 10px;
    padding: 50px 50px 40px;
  }
  &__item-title {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    margin-bottom: 15px;
    font-weight: 500;
  }
  &__item-rate {
    margin-bottom: 35px;
  }
  &__item-text {
    margin-bottom: 35px;
    font-size: 14px;
    line-height: 180%;
    color: #333;
  }
  &__item-date {
    font-size: 14px;
    line-height: 180%;
    color: #808080;
  }
}
