.review-form {
  position: relative;
  padding-top: 50px;
  border-top: 1px solid #c4c4c4;
  @include response(1200px) {
    max-width: 70vw;
    width: 100%;
    margin: 0 auto;
  }
  @include response(767px) {
    max-width: 100%;
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 49px;
    @include response(1200px) {
      flex-direction: column;
      margin-bottom: 20px;
    }
  }
  &__head-title {
    flex: 0 0 auto;
    margin-right: 30px;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    @include response(1200px) {
      margin-right: 0;
      margin-bottom: 10px;
    }
  }
  &__head-checkbox {
    flex: 0 0 auto;
    display: flex;
  }
  &__head-checkbox-item {
    margin-right: 30px;
    &:last-child {
      margin-right: 0;
    }
  }
  &__row {
    position: relative;
    display: flex;
    justify-content: center;
    margin-left: -20px;
    margin-bottom: 15px;
    @include response(1200px) {
      display: block;
      margin-left: 0;
    }
  }
  &__cell {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    width: 100%;
    margin-left: 20px;
    position: relative;
    @include response(1200px) {
      max-width: 100%;
      margin-left: 0;
      margin-bottom: 15px;
    }
  }
  &__rate {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 17px 30px;
    background: #f8f8f8;
    border-radius: 10px;
    @include response(767px) {
      padding: 17px;
    }
  }
  &__rate-title {
    font-size: 16px;
    line-height: 160%;
    color: #333333;
    @include response(767px) {
      font-size: 15px;
    }
  }
  &__write {
    position: relative;
    margin-bottom: 30px;
  }
  &__footer {
    display: flex;
    align-items: center;
    justify-content: center;
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__footer-checkbox {
    flex: 0 0 225px;
    max-width: 225px;
    width: 100%;
    margin-right: 40px;
    @include response(767px) {
      flex: 0 0 auto;
      margin-right: 0;
      margin-bottom: 20px;
    }
    .checkbox__text {
      font-size: 12px;
      line-height: 19px;
    }
  }
}
