.rate[data-rate="1"] .rate__item:nth-child(n + 2),
.rate[data-rate="2"] .rate__item:nth-child(n + 3),
.rate[data-rate="3"] .rate__item:nth-child(n + 4),
.rate[data-rate="4"] .rate__item:nth-child(n + 5),
.rate[data-rate="5"] .rate__item:nth-child(n + 6),
.rate[data-rate="0"] .rate__item {
  fill: rgba($purple,.2)
}
.rate {
  display: flex;
  justify-content: center;
  .product & {
    margin-bottom: 15px;
  }
  &--relative {
    position: relative;
    top: auto;
    right: auto;
    & + .rate-counter {
      margin-left: 8px;
    }
  }
  &__item {
    margin-right: 5px;
    fill: $purple;
    width: 17px;
    height: 16px;
    @include response(767px) {
      width: 12px;
      height: 11px;
      margin-right: 2px;
    }
    svg {
      width: 100%;
      height: 100%;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

.rate-counter {
  font-size: 13px;
  color: $purple;
  font-weight: 700;
}