.featured-products {
  position: relative;
  padding-top: 75px;
  padding-bottom: 55px;
  @include response(767px) {
    padding-top: 40px;
  }
  &--bg {
    background: #f8f8f8;
  }
  &--shop {
    @include response(1200px) {
      display: none;
    }
  }
  &__title {
    margin-bottom: 50px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__slider {
    position: relative;
    margin-left: -19px;
    margin-right: -19px;
    .slick-slide {
      margin-left: 10px;
      margin-right: 10px;
    }
    .slick-list {
      padding-top: 20px;
      padding-bottom: 40px;
      padding: 20px 9px 40px;
      @include response(1200px) {
        padding-bottom: 10px;
      }
      @include response(767px) {
        overflow: visible;
        padding-left: 20vw;
        padding-right: 20vw;
      }
    }
    .slick-arrow {
      position: absolute;
      cursor: pointer;
      z-index: 1;
      display: flex;
      top: 50%;
      margin-top: -41px;
      @include desktop-hover() {
        svg {
          fill: #333;
        }
      }
      svg {
        fill: #a6b6b6;
      }
    }
    .slick-prev {
      left: -70px;
      @include response(1400px) {
        left: -40px;
      }
    }
    .slick-next {
      right: -70px;
      @include response(1400px) {
        right: -40px;
      }
      svg {
        transform: scaleX(-1);
      }
    }
  }
  &__item {
    position: relative;
  }
}
