.slick-dots {
  list-style: none;
  padding: 15px;
  margin: 0;
  display: flex;
  justify-content: center;
  li {
    margin-left: 3px;
    margin-right: 3px;
    flex: 0 0 auto;
    display: flex;
    &.slick-active {
      button {
        width: 40px;
        border-color: transparent;
        &:after {
          opacity: 0;
        }
        &::before {
          opacity: 1;
        }
      }
    }
  }
  button {
    position: relative;
    padding: 0;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    transition: 0.3s ease-in-out;
    background: #fff;
    font-size: 0;
    overflow: hidden;
    border: 0;
    outline: none;
    &::before {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
      opacity: 0;
      transition: 0.3s ease-in-out;
      border-radius: 10px;
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      transition: 0.3s ease-in-out;
      border: 1px solid #333;
      border-radius: 10px;
      transition: 0.3s ease-in-out;
    }
  }
}
