.tab {
  display: none;
  &.active {
    display: block;
  }
}
.tabs-1::-webkit-scrollbar {
  display: none;
}
.tabs-1 {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  @include response(767px) {
    margin-left: -25px;
    margin-right: -25px;
    overflow-x: auto;
    padding-left: 25px;
  }
  li {
    position: relative;
    flex: 1 1 auto;
    @include response(767px) {
      flex: 0 0 auto;
    }
  }
  &--review2 {
    li {
      flex: 0 0 auto;
    }
    a {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
  a {
    display: block;
    position: relative;
    padding: 28px 23px;
    font-size: 15px;
    text-align: center;
    line-height: 160%;
    color: #808080;
    background: #f8f8f8;
    border-radius: 5px;
    text-decoration: none;
    transform: scale(0.9);
    @include response(767px) {
      padding: 12px;
    }
    &::before {
      content: "";
      position: absolute;
      height: 5px;
      left: 0;
      right: 0;
      background: linear-gradient(270deg, #ba58e1 0%, #6f49bc 100%);
      bottom: 0;
      transition: 0.3s ease-in-out;
      opacity: 0;
    }
    &.active {
      background: #fff;
      transform: scale(1);
      color: #333;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      &::before {
        opacity: 1;
      }
    }
  }
  &--orange {
    a {
      &::before {
        background: linear-gradient(270deg, #f4cd3f 0%, #ff6b00 100%);
      }
    }
  }
}
