.choose {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 561px;
  margin-left: -14px;
  @include response(1200px) {
    height: auto;
    flex-direction: row;
  }
  &__item {
    padding: 35px 27px 27px 35px;
    border: 1px solid #f7edff;
    border-radius: 10px;
    width: calc(50% - 14px);
    margin-left: 14px;
    margin-bottom: 14px;
    @include response(767px) {
      width: calc(100% - 14px);
    }
    &:nth-child(1) {
      height: 216px;
      @include response(1200px) {
        height: auto;
      }
    }
    &:nth-child(2) {
      height: 317px;
      @include response(1200px) {
        height: auto;
      }
    }
    &:nth-child(3) {
      height: 317px;
      @include response(1200px) {
        height: auto;
      }
    }
    &:nth-child(4) {
      height: 216px;
      @include response(1200px) {
        height: auto;
      }
    }
  }
  &__head {
    display: flex;
    align-items: center;
    margin-bottom: 22px;
  }
  &__icon {
    flex: 0 0 auto;
    margin-right: 12px;
    .text-content & {
      img {
        margin-bottom: 0;
      }
    }
  }
  &__head-text {
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333333;
  }
  &__item-body {
    &.text-content {
      font-size: 14px;
      line-height: 25px;
    }
  }
}
