.error-page {
  position: relative;
  padding-bottom: 100px;
  @include response(1200px) {
    padding-bottom: 50px;
  }
  &__title {
    margin-bottom: 32px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__box {
    display: flex;
    align-items: center;
    max-width: 850px;
    width: 100%;
    margin: 0 auto;
    @include response(1200px) {
      flex-wrap: wrap;
      justify-content: center;
      max-width: 60vw;
    }
    @include response(767px) {
      max-width: 100%;
    }
  }
  &__img {
    flex: 0 0 auto;
    margin-right: 70px;
    @include response(1200px) {
      margin-right: 0;
      margin-bottom: 20px;
    }
    @include response(767px) {
      flex: 0 1 auto;
    }
  }
  &__info {
    flex: 1 1 auto;
    position: relative;
  }
  &__head {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    @include response(1200px) {
      justify-content: center;
    }
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__head-text {
    font-weight: bold;
    font-size: 64px;
    color: #f15a89;
    flex: 0 0 auto;
    margin-right: 20px;
    @include response(767px) {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
  &__head-descriptions {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    font-weight: 500;
  }
  &__text {
    font-size: 14px;
    line-height: 180%;
    color: #333;
    margin-bottom: 20px;
    a {
      color: $purple;
      font-weight: 500;
      @include desktop-hover() {
        text-decoration: none;
      }
    }
  }
  &__form {
    position: relative;
    margin-bottom: 25px;
  }
  &__form-input {
    &.input-style {
      padding-right: 80px;
    }
  }
  &__form-btn {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 80px;
    background: transparent;
    border: 0;
    cursor: pointer;
    border: 0;
    padding: 0;
    outline: none;
    svg {
      fill: $purple;
    }
  }
  &__btns {
    display: flex;
    @include response(1200px) {
      justify-content: center;
    }
  }
  &__btn {
    margin-right: 20px;
    @include response(767px) {
      margin-right: 10px;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &__decor1 {
    position: absolute;
    top: 0;
    right: 0;
    pointer-events: none;
    @include response(1200px) {
      display: none;
    }
  }
  &__decor2 {
    position: absolute;
    left: 0;
    bottom: 0;
    pointer-events: none;
    @include response(1200px) {
      display: none;
    }
  }
}
