.insights {
  position: relative;
  display: flex;
  margin-bottom: 50px;
  background: #f8f8f8;
  border-radius: 5px;
  @include response(767px) {
    display: block;
    margin-left: -25px;
    margin-right: -25px;
    text-align: center;
  }
  &__left {
    flex: 0 0 50%;
    padding: 50px;
    @include response(767px) {
      width: 100%;
      padding: 25px;
    }
  }
  &__title {
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #000000;
    margin-bottom: 33px;
  }
  &__text {
    font-size: 14px;
    line-height: 180%;
    color: #333333;
    margin-bottom: 10px;
  }
  &__attention {
    font-size: 14px;
    line-height: 180%;
    color: #f15a89;
    font-weight: 500;
  }
  &__form {
    flex: 0 0 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    position: relative;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    background: #fff;
    @include response(767px) {
      width: 100%;
      padding: 25px;
      background: transparent;
      box-shadow: none;
    }
  }
}
