.resources {
  position: relative;
  padding-bottom: 75px;
  @include response(1200px) {
    padding-bottom: 50px;
    max-width: 80vw;
    width: 100%;
    margin: 0 auto;
  }
  @include response(767px) {
    max-width: 100%;
  }
  &__title {
    margin-bottom: 34px;
    @include response(1200px) {
      margin-bottom: 20px;
    }
  }
  &__list {
    display: flex;
    margin-left: -78px;
    @include response(1200px) {
      display: block;
    }
  }
  &__item {
    flex: 0 0 calc(50% - 78px);
    max-width: calc(50% - 78px);
    width: 100%;
    margin-left: 78px;
    border-radius: 10px;
    background: $purple;
    padding: 40px 26px 40px 40px;
    display: flex;
    align-items: center;
    position: relative;
    text-decoration: none;
    @include response(1200px) {
      max-width: calc(100% - 78px);
      margin-bottom: 30px;
    }
    @include response(767px) {
      padding: 30px;
    }
    @include response(666px) {
      display: block;
    }
    @include desktop-hover() {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.3);
      .resources__item-img {
        transform: translateX(4px);
      }
    }
    &:nth-child(even) {
      background: #F15A89;
    }
  }
  &__item-decor {
    position: absolute;
    top: 0;
    right: 0;
  }
  &__item-img {
    flex: 0 0 auto;
    margin-right: 60px;
    border-radius: 10px;
    overflow: hidden;
    margin-left: -80px;
    @include response-min(1200px) {
      transition: .3s ease-in-out;
    }
    img {
      width: 100%;
    }
    @include response(1200px) {
      margin-right: 30px;
    }
    @include response(767px) {
      margin-left: 0;
    }
    @include response(666px) {
      display: block;
      margin-right: 0;
      margin-bottom: 15px;
      height: 200px;
    }
  }
  &__item-box {
    flex: 1 1 auto;
  }
  &__item-title {
    display: block;
    margin-bottom: 20px;
    position: relative;
    font-weight: bold;
    font-size: 28px;
    line-height: 45px;
    color: #fff;
    @include response(767px) {
      font-size: 24px;
      line-height: 34px;
      margin-bottom: 10px;
    }
  }
  &__item-text {
    display: block;
    position: relative;
    color: #fff;
    font-size: 16px;
    margin-bottom: 21px;
    line-height: 26px;
    padding-right: 20px;
    @include response(767px) {
      padding-right: 0;
    }
  }
}
