.category-top {
  position: relative;
  padding-top: 37px;
  padding-bottom: 42px;
  @include response(1200px) {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  &__container {
    display: flex;
    @include response(1200px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__left {
    flex: 0 0 515px;
    max-width: 515px;
    width: 100%;
    padding-right: 20px;
    @include response(1200px) {
      flex: 0 0 auto;
      max-width: 100%;
      padding-right: 0;
      margin-bottom: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    @include response(767px) {
      flex-direction: column;
    }
  }
  &__left-top {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    @include response(1200px) {
      justify-content: center;
      margin-bottom: 0;
    }
    @include response(767px) {
      margin-bottom: 10px;
    }
  }
  &__btn {
    margin-left: 75px;
    @include response(1200px) {
      margin-left: 0;
    }
  }
  &__text {
    flex: 1 1 auto;
    padding-top: 10px;
    position: relative;
    font-size: 14px;
    line-height: 180%;
    color: #333333;
    @include response(1200px) {
      padding-top: 0;
      text-align: center;
    }
    p {
      margin: 0;
    }
  }
}
