.social-likes {
  @include response(1200px) {
    display: flex;
  }
  &__icon {
    display: none;
  }
  &__widget {
    margin-bottom: 10px;
    cursor: pointer;
    @include response(1200px) {
      margin-right: 15px;
    }
    @include desktop-hover() {
      .social-likes__hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__button {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 54px;
    height: 54px;
    border-radius: 50%;
    svg {
      fill: #fff;
    }
  }
  &__button_facebook {
    background: #5474b8;
  }
  &__button_twitter {
    background: #73cff4;
  }
  &__button_pinterest {
    background: #f05150;
  }
  &__hint {
    position: absolute;
    left: calc(100% + 10px);
    top: 50%;
    margin-top: -15px;
    padding: 8px 20px;
    white-space: nowrap;
    background: #f8f8f8;
    border-radius: 2px;
    font-size: 12px;
    color: #808080;
    font-weight: 500;
    opacity: 0;
    visibility: hidden;
    transition: .3s linear;
  }
}
