.medical {
  position: relative;
  padding-top: 30px;
  border-top: 1px solid #ececec;
  padding-left: 40px;
  padding-right: 40px;
  margin-top: 30px;
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 160%;
    color: #333;
    margin-bottom: 8px;
  }
  &__text {
    font-size: 14px;
    line-height: 180%;
    color: #808080;
    p {
      margin: 0;
    }
  }
}
