.breadcrumb {
  list-style: none;
  margin: 0;
  padding: 0 20px;
  display: inline-flex;
  flex-wrap: wrap;
  background: #f8f8f8;
  border-radius: 4px;
  @include response(1200px) {
    display: none;
  }
  li {
    margin-right: 55px;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: block;
    position: relative;
    font-size: 12px;
    line-height: 19px;
    color: #333;
    text-decoration: none;
    padding-top: 10px;
    padding-bottom: 10px;
    @include desktop-hover() {
      text-decoration: underline;
    }
    &::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 1px;
      background: #808080;
      right: -40px;
      top: 19px;
    }
  }
  span {
    display: block;
    font-size: 12px;
    line-height: 19px;
    color: #333;
    opacity: 0.5;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  &--new {
    padding: 0;
    a {
      font-weight: 500;
      font-size: 13px;
      line-height: 160%;
      color: #fff;
      padding: 6px 22px;
      background: $purple;
      border-radius: 4px;
      &::before {
        display: none;
      }
    }
  }
}
