.contacts {
  position: relative;
  padding-bottom: 75px;
  &__title {
    margin-bottom: 30px;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__box {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include response(1200px) {
      flex-direction: column;
      max-width: 580px;
      width: 100%;
      margin: 0 auto;
    }
    @include response(666px) {
      max-width: 100%;
    }
  }
  &__info {
    flex: 0 0 580px;
    max-width: 580px;
    width: 100%;
    background: #f8f8f8;
    border-radius: 10px;
    padding: 60px 75px 75px;
    @include response(1200px) {
      margin-top: 30px;
      order: 2;
    }
    @include response(666px) {
      max-width: 100%;
      padding: 20px;
    }
  }
  &__info-title {
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #000;
    font-weight: 500;
    margin-bottom: 40px;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__holder {
    flex: 0 0 580px;
    max-width: 580px;
    width: 100%;
    background: #fff;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 60px 103px 50px;
    @include response(666px) {
      max-width: 100%;
      padding: 20px;
    }
  }
  &__form {
    position: relative;
  }
  &__form-title {
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 160%;
    font-weight: 500;
    text-align: center;
    color: #333;
  }
  &__form-description {
    margin-bottom: 40px;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #333;
  }
  &__form-list {
    position: relative;
    margin-bottom: 20px;
  }
  &__form-row {
    position: relative;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__form-controls {
    display: flex;
    align-items: center;
    @include response(666px) {
      display: block;
    }
  }
  &__form-btn {
    flex: 0 0 auto;
    margin-left: 40px;
    @include response(666px) {
      margin-left: 0;
      margin-top: 20px;
    }
  }
}
