.chapters {
  position: relative;
  background: #f8f8f8;
  margin-bottom: 50px;
  overflow: hidden;
  &__box {
    max-width: 880px;
    width: 100%;
    margin: 0 auto;
    padding-top: 50px;
    padding-bottom: 30px;
    &--full {
      max-width: 1180px;
    }
    @include response(767px) {
      padding-top: 40px;
      padding-bottom: 20px;
    }
  }
  &__title {
    margin-bottom: 40px;
    position: relative;
    font-size: 20px;
    line-height: 160%;
    text-align: center;
    color: #333;
    font-weight: 500;
    @include response(767px) {
      margin-bottom: 20px;
    }
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: -20px;
    @include response(767px) {
      display: block;
      margin-left: 0;
      max-width: 360px;
      width: 100%;
      margin: 0 auto;
    }
  }
  &__item {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    width: 100%;
    margin-left: 20px;
    margin-bottom: 20px;
    background: #fff;
    border-radius: 30px 10px 10px 10px;
    border: 1px solid #e9e9e9;
    text-align: center;
    position: relative;
    padding-bottom: 30px;
    overflow: hidden;
    text-decoration: none;
    .chapters__box--full & {
      flex: 0 0 calc(25% - 20px);
      max-width: calc(25% - 20px);
      @include response(1200px) {
        flex: 0 0 calc(33.333% - 20px);
        max-width: calc(33.333% - 20px);
      }
      @include response(767px) {
        max-width: 100%;
      }
    }
    @include desktop-hover() {
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      .chapters__item-btn {
        background: #f4bbb3;
        color: #333;
        svg {
          fill: #333;
        }
        &::before {
          opacity: 0;
        }
      }
    }
    @include response(767px) {
      margin-left: 0;
      margin-bottom: 8px;
      max-width: 100%;
      display: flex;
      align-items: center;
      border-radius: 5px;
      padding: 14px 10px 14px 28px;
    }
  }
  &__item-number {
    position: absolute;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    top: 16px;
    left: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    line-height: 11px;
    text-align: center;
    color: #333;
    @include response(767px) {
      position: relative;
      width: 40px;
      height: 40px;
      flex: 0 0 40px;
      margin-right: 20px;
      top: auto;
      left: auto;
    }
  }
  &__item-img {
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    @include response(767px) {
      display: none;
    }
    img {
      max-height: 100%;
    }
  }
  &__item-title {
    display: block;
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
    line-height: 160%;
    color: #333;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
    padding-left: 15px;
    padding-right: 15px;
    @include response(767px) {
      margin-bottom: 0;
      padding-left: 0;
      padding-right: 0;
    }
  }
  &__item-btn {
    display: inline-flex;
    align-items: center;
    padding: 7px 20px;
    border-radius: 5px;
    font-size: 13px;
    line-height: 160%;
    color: #a6b6b6;
    transition: 0.3s ease-in-out;
    position: relative;
    text-decoration: none;
    @include response(767px) {
      display: none;
    }
    &::before {
      content: "";
      position: absolute;
      left: 20px;
      right: 20px;
      bottom: 0;
      height: 1px;
      background: #a6b6b6;
      transition: 0.3s ease-in-out;
    }
    svg {
      fill: #a6b6b6;
      flex: 0 0 auto;
      margin-left: 10px;
    }
  }
  &__item-decor {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    max-width: 100%;
    height: auto;
    pointer-events: none;
    @include response(767px) {
      transform: rotate(-45deg);
      right: -66px;
      left: auto;
      top: 5px;
      width: 210px;
    }
  }
  &__decor {
    position: absolute;
    bottom: 0;
    left: 0;
    pointer-events: none;
    &--right {
      left: auto;
      right: 0;
      transform: scaleX(-1);
      @include response(767px) {
        display: none;
      }
    }
  }
}
