.main-products {
  position: relative;
  &__row {
    position: relative;
    background: #f8f8f8;
    margin-bottom: 67px;
    &:nth-child(even) {
      background: #fff;
      box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
      .main-products__box {
        flex-direction: row-reverse;
      }
      .main-products__info {
        margin-right: 0;
        margin-left: 70px;
        @include response(1200px) {
          margin-left: 0;
        }
      }
      .main-products__decor {
        left: auto;
        right: 0;
      }
      .main-products__row-decor {
        right: auto;
        left: calc(100% + 10px);
      }
    }
  }
  &__row-decor {
    position: absolute;
    right: calc(100% + 10px);
    bottom: 0;
  }
  &__box {
    position: relative;
    padding-top: 75px;
    padding-bottom: 75px;
    display: flex;
    @include response(1200px) {
      padding-top: 50px;
      padding-bottom: 50px;
      display: block;
    }
  }
  &__info {
    flex: 1 1 auto;
    margin-right: 70px;
    padding-top: 30px;
    @include response(1200px) {
      margin-right: 0;
      padding-top: 0;
      margin-bottom: 30px;
    }
  }
  &__title-box {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    position: relative;
    z-index: 1;
    text-decoration: none;
    color: #333;
    @include desktop-hover() {
      color: $purple;
      .main-products__title-img {
        transform: scale(0.9);
      }
    }
  }
  &__title-img {
    flex: 0 0 75px;
    max-width: 75px;
    width: 100%;
    display: flex;
    margin-right: 30px;
    transition: 0.3s ease-in-out;
    @include response(1200px) {
      margin-right: 20px;
    }
    &--small {
      flex: 0 0 54px;
      max-width: 54px;
      margin-right: 20px;
      @include response(1200px) {
        flex: 0 0 24px;
        max-width: 24px;
        & + .main-products__title {
          font-size: 20px;
          line-height: 32px;
        }
      }
    }
  }
  &__title {
    font-weight: bold;
    font-size: 40px;
    line-height: 50px;
    @include response(1200px) {
      font-size: 30px;
      line-height: 40px;
    }
    @include response(767px) {
      font-size: 24px;
      line-height: 34px;
    }
  }
  &__text {
    position: relative;
    padding-left: 105px;
    z-index: 1;
    @include response(1200px) {
      padding-left: 0;
    }
  }
  &__decor {
    position: absolute;
    left: 0;
    bottom: 0;
  }
  &__list-box {
    flex: 0 0 700px;
    max-width: 700px;
    width: 100%;
    position: relative;
  }
  &__list {
    margin-left: -20px;
    display: flex;
    @include response(666px) {
      margin-left: -4px;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
}
