.social {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  &__item {
    position: relative;
  }
  &__link {
    display: flex;
    padding: 7px;
    @include desktop-hover() {
      transform: translateY(-2px);
      svg {
        fill: $purple;
      }
    }
    svg {
      fill: #333;
    }
  }
}
