.product-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -20px;
  margin-bottom: 35px;
  @include response(767px) {
    margin-left: -4px;
  }
}
.product {
  position: relative;
  background: #fff;
  border: 1px solid #e9e9e9;
  border-radius: 5px;
  outline: none;
  padding: 50px 15px 23px;
  flex: 0 0 calc(25% - 20px);
  max-width: calc(25% - 20px);
  width: 100%;
  margin-left: 20px;
  margin-bottom: 20px;
  @include response-min(1200px) {
    transition: 0.3s ease-in-out;
  }
  @include response(1500px) {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
  }
  @include response(767px) {
    flex: 0 0 calc(50% - 4px);
    max-width: calc(50% - 4px);
    margin-left: 4px;
    margin-bottom: 4px;
    padding-top: 15px;
    padding-bottom: 20px;
  }
  .slick-slide & {
    max-width: 100%;
    margin-left: 0;
    margin-bottom: 0;
  }
  .main-products__list & {
    flex: 0 0 calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
    padding-left: 10px;
    padding-right: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 0;
    @include response(767px) {
      flex: 0 0 calc(50% - 4px);
      max-width: calc(50% - 4px);
      margin-left: 4px;
      margin-bottom: 4px;
      padding-bottom: 15px;
    }
  }
  @include desktop-hover() {
    box-shadow: 0px 5px 30px rgba(0, 0, 0, 0.05);
    border-color: #fff;
    .product__btn {
      color: #fff;
      svg {
        fill: #fff;
      }
      span {
        display: block;
      }
      &::before {
        opacity: 1;
        transform: scaleX(1);
      }
    }
  }
  &__label {
    position: absolute;
    top: 15px;
    left: 15px;
    text-transform: uppercase;
    margin-bottom: 15px;
    display: inline-block;
    color: #fff;
    text-align: center;
    font-weight: bold;
    font-size: 12px;
    padding: 5px 12px;
    border-radius: 15px;
    background: #f15a89;
    @include response(1200px) {
      font-size: 10px;
      left: 20px;
      top: 20px;
    }
    &--sale {
      background: #f15a89;
    }
    &--new {
      background: #333;
    }
  }
  &__img {
    display: block;
    margin-bottom: 15px;
    border-radius: 10px;
    overflow: hidden;
    outline: none;
    .product--out-stock & {
      opacity: 0.4;
      pointer-events: none;
    }
    @include response(767px) {
      margin-bottom: 10px;
    }
    @include desktop-hover() {
      img {
        transform: scale(1.02);
      }
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: 0.3s ease-in-out;
    }
    & + .product__title {
      margin-top: 46px;
      @include response(767px) {
        margin-top: 36px;
      }
    }
  }
  &__title {
    display: block;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: #333;
    margin-bottom: 10px;
    text-decoration: none;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-weight: 500;
    .product--out-stock & {
      pointer-events: none;
      opacity: 0.4;
    }
    @include response(767px) {
      font-size: 12px;
    }
    @include desktop-hover() {
      text-decoration: underline;
    }
  }
  &__price-box {
    display: flex;
    align-items: center;
    justify-content: center;
    .product--out-stock & {
      opacity: 0.4;
      pointer-events: none;
    }
  }
  &__price-new {
    font-size: 20px;
    line-height: 160%;
    color: #f15a89;
    flex: 0 0 auto;
    margin-right: 7px;
    font-weight: 500;
    @include response(767px) {
      font-size: 16px;
    }
  }
  &__price {
    font-size: 20px;
    line-height: 160%;
    color: #333;
    flex: 0 0 auto;
    font-weight: 500;
    @include response(767px) {
      font-size: 16px;
    }
  }
  &__price-old {
    font-size: 16px;
    line-height: 160%;
    text-decoration-line: line-through;
    color: #a6b6b6;
    flex: 0 0 auto;
    @include response(767px) {
      font-size: 14px;
    }
  }
  &__btn-box {
    position: relative;
    text-align: center;
    padding-left: 17px;
    padding-right: 17px;
    margin-top: 15px;
    @include response(1200px) {
      display: none;
    }
    @include response(1023px) {
      padding-left: 0;
      padding-right: 0;
    }
    @include response(767px) {
      margin-top: 10px;
    }
  }
  &__btn {
    width: 100%;
    @include response(767px) {
      padding-left: 12px;
      padding-right: 12px;
      svg {
        display: none;
      }
      span {
        margin-left: 0;
      }
    }
  }
  &__out-stock {
    height: 52px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 20px;
    color: #333;
    font-weight: 500;
    @include response(767px) {
      font-size: 18px;
    }
  }
}
