.pagination {
  position: relative;
  list-style: none;
  padding: 0 0 50px;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  li {
    position: relative;
    margin-left: 5px;
    margin-right: 5px;
    &:last-child {
      a {
        background: transparent;
      }
      svg {
        transform: scaleX(-1);
      }
    }
    &:first-child {
      a {
        background: transparent;
      }
    }
    &.disabled {
      opacity: 0.3;
      pointer-events: none;
    }
  }
  a {
    display: block;
    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    border-radius: 4px;
    background: #a6b6b6;
    @include desktop-hover() {
      background: $purple;
      color: #fff;
      svg {
        stroke: #fff;
      }
    }
    svg {
      fill: #000;
    }
  }
  span {
    width: 25px;
    height: 25px;
    border-radius: 4px;
    display: block;
    font-weight: 500;
    display: flex;
    align-items: center;
    color: #fff;
    justify-content: center;
    font-size: 14px;
    background: $purple;
  }
}
