.blog-product-list {
  position: relative;
  margin-bottom: 50px;
}
.blog-product {
  position: relative;
  background: #fff;
  box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  padding: 40px;
  margin-bottom: 20px;
  @include response(767px) {
    margin-left: -25px;
    margin-right: -25px;
    padding: 40px 25px 59px;
    margin-bottom: 0;
    border-bottom: 1px solid $purple;
    border-radius: 0;
  }
  &__head {
    display: flex;
    align-items: flex-start;
    margin-bottom: 30px;
    @include response(767px) {
      display: block;
    }
  }
  &__title {
    margin-bottom: 26px;
    font-weight: bold;
    font-size: 28px;
    line-height: 160%;
    color: #333333;
    @include response(767px) {
      font-size: 20px;
      margin-bottom: 20px;
      text-align: center;
    }
  }
  &__img {
    flex: 0 0 348px;
    max-width: 348px;
    width: 100%;
    margin-right: 40px;
    box-shadow: 0px 5px 40px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    overflow: hidden;
    @include response(767px) {
      max-width: 100%;
      margin-right: 0;
      margin-bottom: 28px;
      img {
        width: 100%;
      }
    }
  }
  &__head-box {
    flex: 1 1 auto;
  }
  &__head-text {
    &.text-content {
      font-size: 14px;
      line-height: 25px;
    }
  }
  &__body {
    position: relative;
    margin-bottom: 26px;
    padding-bottom: 26px;
    border-bottom: 1px solid $purple;
    &--red {
      border-bottom-color: #f15a89;
      margin-bottom: 0;
    }
    @include response(767px) {
      border-bottom: 0;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }
  &__body-info {
    position: relative;
    display: flex;
    align-items: flex-end;
    margin-bottom: 35px;
    @include response(767px) {
      margin-bottom: 6px;
    }
    &::after {
      content: "";
      position: relative;
      display: inline-block;
      height: 1px;
      flex: 1 1 auto;
      background: $purple;
      margin-left: 30px;
      @include response(767px) {
        display: none;
      }
    }
    &--red {
      &::after {
        background: #f15a89;
      }
    }
  }
  &__body-info-text {
    display: flex;
    align-items: center;
    border-radius: 5px;
    background: $purple;
    padding: 10px 22px;
    color: #fff;
    font-weight: 500;
    font-size: 13px;
    line-height: 160%;
    &--red {
      background: #f15a89;
      @include response(767px) {
        color: #f15a89 !important;
      }
    }
    @include response(767px) {
      background: transparent;
      font-size: 16px;
      color: #333;
      padding: 0;
      font-weight: bold;
    }
    svg {
      flex: 0 0 auto;
      margin-left: 10px;
      @include response(767px) {
        display: none;
      }
    }
  }
  &__text {
    @include response(767px) {
      height: 100px;
      overflow: hidden;
      &.active {
        height: auto;
      }
    }
  }
  &__mobile-btn {
    display: none;
    @include response(767px) {
      display: inline-block;
      vertical-align: top;
      margin-top: 28px;
      font-size: 16px;
      text-transform: uppercase;
      font-weight: 700;
      color: $purple;
      border-bottom: 1px solid $purple;
    }
    .close {
      display: none;
    }
    &.active {
      .open {
        display: none;
      }
      .close {
        display: block;
      }
    }
  }
  &__btn-box {
    text-align: center;
    @include response(767px) {
      margin-bottom: 25px;
      .btn-1 {
        width: 100%;
      }
    }
    &--no-js {
      text-align: left;
      margin-top: 40px;
    }
  }
  &__body-info-text-icon {
    flex: 0 0 16px;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #fff;
    text-align: center;
    font-size: 13px;
    border-radius: 50%;
    margin-left: 12px;
    @include response(767px) {
      display: none;
    }
  }
  &__body-holder {
    margin-bottom: 36px;
    &--main {
      padding-bottom: 26px;
      border-bottom: 1px solid $purple;
      @include response(767px) {
        border-bottom-color: #e0e0e0;
      }
    }
    @include response(767px) {
      border-bottom: 1px solid #e0e0e0;
      padding-bottom: 26px;
    }
    &:last-child {
      margin-bottom: 0;
      @include response(767px) {
        border-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
  &__body-holder-title {
    margin-bottom: 16px;
    font-weight: bold;
    font-size: 20px;
    line-height: 160%;
    color: #333333;
    @include response(767px) {
      font-size: 16px;
      margin-bottom: 6px;
    }
  }
}
