.star-rating {
  position: relative;
  display: flex;
  align-items: center;
  &__item {
    display: flex;
    cursor: pointer;
    transition: 0.15s linear;
    .active.star-rating & {
      transform: scale(1.1);
    }
    @include desktop-hover() {
      svg {
        fill: rgba($purple, 0.5);
      }
    }
    & + .star-rating__item {
      margin-left: 15px;
    }
    svg {
      fill: rgba($purple, 0.2);
    }
    &.active {
      svg {
        fill: $purple;
      }
    }
  }
  &__value {
    display: none;
  }
}
